@import url('https://fonts.googleapis.com/css2?family=Jim+Nightshade&display=swap');
/* font-family: 'Jim Nightshade', cursive; */

@font-face {
    font-family: 'Mistery';
    src: url('../../../public/fonts/StrangerbackintheNight.ttf');
  }
  

.MenuSection {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 8%;
    /* border: 2px solid red; */
}

.Menuboxes {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    height: 100%;
    /* border: 2px solid yellow; */
}

.btn-21 {
    cursor: pointer;
    letter-spacing: 2px;
    text-transform: uppercase;
    /* font-family: 'Jim Nightshade', cursive; */
    font-family: 'Mistery', cursive;
    font-size: 2.3rem;
    width: 200px;
    height: 45px;
    position: relative;
    color: #c7c7c7;
    z-index: 1;
    transition: all 0.5s ease-in-out;
    border-radius: 0px;
    background: transparent;
    padding: 0;
    overflow: hidden;
    border: none;
  }
  
  .btn-21:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(to right, transparent, #bbb);
    animation: btn-21-right 2s linear infinite;
    transition: 0.5s ease-in-out;
  }
  
  .btn-21:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(to bottom, transparent, #bbb);
    animation: btn-21-bottom 2s linear infinite;
    animation-delay: 1s;
    transform: translateY(-100%);
  }
  
  .btn-21 span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 4px;
  }
  
  .btn-21 span:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(to left, transparent, #bbb);
    animation: btn-21-left 2s linear infinite;
  }
  
  .btn-21 span:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(to top, transparent, #bbb);
    animation: btn-21-top 2s linear infinite;
    animation-delay: 1s;
    transform: translateY(-100%);
  }
  
  .btn-21:hover:before {
    background: linear-gradient(to right, transparent, red);
  }
  
  .btn-21:hover:after {
    background: linear-gradient(to bottom, transparent, red);
  }
  
  .btn-21 span:hover:before {
    background: linear-gradient(to top, transparent, red);
  }
  
  .btn-21 span:hover:after {
    background: linear-gradient(to left, transparent, red);
  }
  

  .btn-21:hover.mist {
    color: rgb(229, 255, 0);
  }

  .btn-21:hover.chart {
    color: rgb(255, 145, 0);
  }
  .btn-21:hover.twitter {
    color: #00a7fc;
  }
  
  .btn-21:active:before {
    background: linear-gradient(to right, transparent, #00a7fc);
  }
  
  .btn-21:active:after {
    background: linear-gradient(to bottom, transparent, #00a7fc);
  }
  
  .btn-21 span:active:before {
    background: linear-gradient(to top, transparent, #00a7fc);
  }
  
  .btn-21 span:active:after {
    background: linear-gradient(to left, transparent, #00a7fc);
  }
  
  .btn-21:active {
    color: #00a7fc;
  }
  
  @keyframes btn-21-right {
    0% {
      transform: translateX(-100%);
    }
  
    100% {
      transform: translateX(100%);
    }
  
  }
  
  @keyframes btn-21-top {
    0% {
      transform: translateY(100%);
    }
  
    100% {
      transform: translateY(-100%);
    }
  
  }
  
  @keyframes btn-21-left {
    0% {
      transform: translateX(100%);
    }
  
    100% {
      transform: translateX(-100%);
    }
  
  }
  
  @keyframes btn-21-bottom {
    0% {
      transform: translateY(-100%);
    }
  
    100% {
      transform: translateY(100%);
    }
  
  }
  
  .btn-21:focus {
    outline: none
  }
  